.app-container {
  min-width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: center;
  scroll-behavior: smooth;
  scroll-snap-type: mandatory;
}

.bgVideo {
  width: 100%;
  height: 100svh;
  top: 0;
  left: 0;
  position: fixed;
  z-index: -1;
}
video {
  min-width: 100%;
  min-height: 100svh;
  object-fit: cover;
}

.bgVideo::after {
  content: "";
  position: absolute;
  background-color: rgba(5, 5, 5, 0.93);
  width: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
}
.sectionWrapper {
  z-index: 10;
  width: 100%;
  height: 100%;
  padding-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
