.contactContainer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}

.contactLeft > form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  gap: 1rem;
}

.contactLeft > form > .formInformation {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 1rem;
}

.contactLeft > form > label {
  width: 100%;
  text-align: left;
}

.contactLeft > form > .formInformation > input {
  padding: 5px;
  border: none;
  border-radius: 5px;
}

.contactLeft > form > .formInformation > textarea {
  height: 100px;
  padding: 5px;
}

.submitBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
  width: 90px;
  height: 40px;
  border-radius: 10px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-shadow: 2px 3px 20px purple;
}
.submitBtn > p,
.submitBtn > p > svg {
  color: rgb(241, 238, 238);
  font-size: 1.2rem;
}
