.skillcontainer {
  width: 100%;
  height: 600vh;
  position: relative;
}

.skilltitleicon {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: start;
  position: sticky;
  top: 0;
  height: 30vh;
  width: 100%;
  z-index: 10;
}

.skilltitleicon > p {
  font-size: 2.5rem;
}

.skillprogressbar {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.skillprogressbar > .flipContainer {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.skillprogressbar > div > .flipInner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  background-color: transparent;
  width: 70%;
  height: 300px;
  padding: 10px;
  border-radius: 10px;
  transform-style: preserve-3d;
  overflow: hidden;
  position: relative;
}

.skillsRateTitle > h1 {
  font-size: 1.2rem;
}

.fronPage,
.backFlip {
  width: 100%;
  height: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.backFlip {
  transform: rotateY(-180deg);
  border-radius: 10px;
  padding-top: 10px;
  color: white;
  background-color: blueviolet;
}

.backFlip > h1 {
  font-size: 1.2rem;
}

.backFlip > p {
  font-size: 0.9rem;
  font-family: sans-serif;
  text-align: justify;
  word-wrap: break-word;
}
