.personal-container {
  width: 100%;
  height: 100vh;
  z-index: 10;
}

.perSonalWrapper {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;

  gap: 1rem;
}

.perSonalWrapper > h1 {
  text-align: center;
}

.personal-card {
  height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5px;
  gap: 1rem;
}

.personal-card > h1 {
  font-size: 2rem;
  text-align: center;
  z-index: 10;
}
.img-container {
  top: 250px;
  left: 130px;
  width: 180px;
  height: 180px;
  display: flex;
  overflow: hidden;
  border-radius: 50%;
}
.img-container > img {
  width: 100%;
  height: 100%;
}

.contact-info {
  width: 80%;
  height: 300px;
  align-self: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  gap: 1rem;
  border: none;
  border-radius: 10px;
  box-shadow: 5px -10px 200px rgb(233, 46, 233);
}

.contact-info > ul {
  display: flex;
  flex-direction: column;

  gap: 1rem;
  width: 100%;
}

.contact-info > ul > li {
  width: 100%;
  list-style-type: none;
  display: flex;
  overflow: hidden;
  gap: 2rem;
  cursor: pointer;
}

.contact-info > ul > li > h4 {
  width: 100px;
  font-size: 1rem;
  font-weight: 400;
}

.contact-info > ul > li > p {
  font-size: 1rem;
  width: 30px;
  overflow: hidden;
}

.contact-info > ul > li > p:nth-child(3) {
  width: 150px;
  overflow: hidden;
  display: flex;
  justify-content: start;
  text-align: left;
  flex-wrap: nowrap;
  font-family: sans-serif;
  font-size: 0.9rem;
}
