.progressContainer {
  width: 140px;
  height: auto;
  position: relative;
  z-index: 10;
}
.outerCircle {
  width: 160px;
  height: 160px;
  border: 3px solid rgb(219, 212, 212);
  border-radius: 50%;
  padding: 18px;
}

.innerCircle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 3px solid rgb(247, 240, 240);
  display: flex;
  align-items: center;
  justify-content: center;
}

#number {
  font-weight: 600;
  color: purple;
}

.progressContainer > svg {
  height: 100%;
  position: absolute;
  top: 0;
  left: 0px;
}

circle {
  fill: none;
  stroke: url(#gradientColor);
  stroke-width: 17px;
}
